.password-protection {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Inter', sans-serif;
}

.password-box {
  background: white;
  padding: 3rem;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 90%;
  text-align: center;
}

.password-box h1 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.75rem;
  font-weight: 600;
}

.password-box p {
  margin: 0 0 2rem 0;
  color: #666;
  font-size: 0.95rem;
}

.password-box form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.password-box input[type="password"] {
  padding: 0.875rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  font-family: 'Inter', sans-serif;
}

.password-box input[type="password"]:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.password-box input[type="password"].error {
  border-color: #ef4444;
  animation: shake 0.4s;
}

.password-box button {
  padding: 0.875rem 1.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  font-family: 'Inter', sans-serif;
}

.password-box button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.3);
}

.password-box button:active {
  transform: translateY(0);
}

.error-message {
  color: #ef4444;
  font-size: 0.875rem;
  margin: -0.5rem 0 0 0;
  text-align: left;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-10px); }
  75% { transform: translateX(10px); }
}

@media (max-width: 480px) {
  .password-box {
    padding: 2rem;
  }

  .password-box h1 {
    font-size: 1.5rem;
  }
}
